/**
 * Generic API Query Hook
 *
 * Provides a reusable hook for fetching data with caching, loading states,
 * and error handling. Works with any async function.
 */

import { useState, useEffect, useCallback, useRef } from "react";

/**
 * Query state
 */
export interface QueryState<T> {
  /** The fetched data */
  data: T | null;
  /** Loading state */
  isLoading: boolean;
  /** Error if any */
  error: Error | null;
  /** Whether data has been fetched at least once */
  isSuccess: boolean;
  /** Whether the query is currently fetching (includes refetches) */
  isFetching: boolean;
}

/**
 * Query options
 */
export interface UseApiQueryOptions<T> {
  /** Whether to enable the query */
  enabled?: boolean;
  /** Initial data */
  initialData?: T;
  /** Stale time in ms (default: 0 - always stale) */
  staleTime?: number;
  /** Cache time in ms (default: 5 minutes) */
  cacheTime?: number;
  /** Retry count (default: 3) */
  retryCount?: number;
  /** Retry delay in ms (default: 1000) */
  retryDelay?: number;
  /** On success callback */
  onSuccess?: (data: T) => void;
  /** On error callback */
  onError?: (error: Error) => void;
  /** Refetch on window focus (default: false) */
  refetchOnWindowFocus?: boolean;
}

/**
 * Query result
 */
export interface UseApiQueryResult<T> extends QueryState<T> {
  /** Manually refetch the data */
  refetch: () => Promise<void>;
  /** Reset the query state */
  reset: () => void;
}

// Simple in-memory cache
const queryCache = new Map<string, { data: unknown; timestamp: number }>();

/**
 * Generic API query hook
 *
 * @param queryKey - Unique key for the query (used for caching)
 * @param queryFn - Async function to fetch data
 * @param options - Query options
 * @returns Query result with data, loading state, and refetch function
 *
 * @example
 * ```typescript
 * const { data, isLoading, error, refetch } = useApiQuery(
 *   ["products", categoryId],
 *   () => api.products.list({ categoryId }),
 *   { staleTime: 60000 }
 * );
 * ```
 */
export function useApiQuery<T>(
  queryKey: unknown[],
  queryFn: () => Promise<T>,
  options: UseApiQueryOptions<T> = {}
): UseApiQueryResult<T> {
  const {
    enabled = true,
    initialData,
    staleTime = 0,
    cacheTime = 5 * 60 * 1000,
    retryCount = 3,
    retryDelay = 1000,
    onSuccess,
    onError,
    refetchOnWindowFocus = false,
  } = options;

  const cacheKey = JSON.stringify(queryKey);
  const retryCountRef = useRef(0);

  const [state, setState] = useState<QueryState<T>>(() => {
    // Check cache first
    const cached = queryCache.get(cacheKey);
    if (cached && Date.now() - cached.timestamp < cacheTime) {
      return {
        data: cached.data as T,
        isLoading: false,
        error: null,
        isSuccess: true,
        isFetching: false,
      };
    }

    return {
      data: initialData ?? null,
      isLoading: enabled,
      error: null,
      isSuccess: false,
      isFetching: enabled,
    };
  });

  const fetchData = useCallback(
    async (isRefetch = false) => {
      if (!isRefetch) {
        // Check if cached data is still fresh
        const cached = queryCache.get(cacheKey);
        if (cached && Date.now() - cached.timestamp < staleTime) {
          setState((prev) => ({
            ...prev,
            data: cached.data as T,
            isLoading: false,
            isSuccess: true,
            isFetching: false,
          }));
          return;
        }
      }

      setState((prev) => ({
        ...prev,
        isLoading: !prev.data,
        isFetching: true,
        error: null,
      }));

      try {
        const data = await queryFn();

        // Update cache
        queryCache.set(cacheKey, { data, timestamp: Date.now() });

        setState({
          data,
          isLoading: false,
          error: null,
          isSuccess: true,
          isFetching: false,
        });

        retryCountRef.current = 0;
        onSuccess?.(data);
      } catch (err) {
        const error = err instanceof Error ? err : new Error(String(err));

        // Retry logic
        if (retryCountRef.current < retryCount) {
          retryCountRef.current++;
          setTimeout(() => fetchData(isRefetch), retryDelay * retryCountRef.current);
          return;
        }

        setState((prev) => ({
          ...prev,
          isLoading: false,
          error,
          isFetching: false,
        }));

        retryCountRef.current = 0;
        onError?.(error);
      }
    },
    [cacheKey, queryFn, staleTime, retryCount, retryDelay, onSuccess, onError]
  );

  const refetch = useCallback(async () => {
    retryCountRef.current = 0;
    await fetchData(true);
  }, [fetchData]);

  const reset = useCallback(() => {
    queryCache.delete(cacheKey);
    setState({
      data: initialData ?? null,
      isLoading: false,
      error: null,
      isSuccess: false,
      isFetching: false,
    });
  }, [cacheKey, initialData]);

  // Initial fetch
  useEffect(() => {
    if (enabled) {
      fetchData();
    }
  }, [enabled, cacheKey]); // eslint-disable-line react-hooks/exhaustive-deps

  // Refetch on window focus
  useEffect(() => {
    if (!refetchOnWindowFocus || !enabled) return;

    const handleFocus = () => {
      const cached = queryCache.get(cacheKey);
      if (!cached || Date.now() - cached.timestamp >= staleTime) {
        fetchData(true);
      }
    };

    window.addEventListener("focus", handleFocus);
    return () => window.removeEventListener("focus", handleFocus);
  }, [refetchOnWindowFocus, enabled, cacheKey, staleTime, fetchData]);

  return {
    ...state,
    refetch,
    reset,
  };
}

/**
 * Clear the query cache
 */
export function clearQueryCache(): void {
  queryCache.clear();
}

/**
 * Invalidate specific query keys
 */
export function invalidateQueries(queryKey: unknown[]): void {
  const cacheKey = JSON.stringify(queryKey);
  queryCache.delete(cacheKey);
}
